.app {
  height: 100vh;

  .sections {
    width: 100%;
    height: calc(100vh - 5rem);
    position: relative;
    top: 5rem;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none; // for firefox
    &::-webkit-scrollbar{
      display: none;
    }

    > * {
      width: 100vw;
      height: calc(100vh - 5rem);
      scroll-snap-align: start;
    }
  }
}